import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelcomeComponent } from './welcome/welcome.component';
import { ProductsComponent } from './products/products.component';
import { EmployeesComponent } from './employees/employees.component';
import { EmployeedetailComponent } from './employeedetail/employeedetail.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { EmployeedetailsGuard } from './employeedetails.guard';

const routes: Routes = [
{ path: '', redirectTo: 'welcome', pathMatch: 'full' },
{ path: 'welcome', component: WelcomeComponent, data: { title: 'Welcome Page'} },
{ path: 'products', component: ProductsComponent },
{ path: 'employees', component: EmployeesComponent },
{ path: 'employeesdetailsguard', component: EmployeedetailsGuard },
{ path: 'employees/:id', component: EmployeedetailComponent, canActivate:[EmployeedetailsGuard]},
{ path: '**', component: PagenotfoundComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule { }
