import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError, Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators'; // This is where I import map operator
import { IEmployee } from './employees.interface';

@Injectable({
  providedIn: 'root'
})
export class EmployeesService {
  //private _employeeUrl = './assets/data.json';
  //private _employeeUrl = 'http://localhost:3000/api/posts';
  private _employeeUrl = 'http://localhost:3000/users';
  employees: IEmployee[];
  constructor(private _http: HttpClient) { }

  getEmpData() {
    return this._http.get<IEmployee[]>(this._employeeUrl).pipe(
      catchError(this.handleError)
    );
  }


  getEmployee(id: number): Observable<IEmployee | undefined> {
    return this.getEmpData().pipe(
      map((employees: IEmployee[]) => employees.find(e => e.empid === id))
    );
  }


  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError('Something bad happened; please try again later.');
  };
}