import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { EmployeesService } from './../employees.service';
import { IEmployee } from './../employees.interface';
//import { EmployeesComponent } from './../employees/employees.component';

@Component({
  selector: 'app-employeedetail',
  templateUrl: './employeedetail.component.html',
  styleUrls: ['./employeedetail.component.css']
})
export class EmployeedetailComponent implements OnInit {
  errorMessage:string="";
  id: string;
  employee: IEmployee;

  constructor(private route: ActivatedRoute, private employeeService: EmployeesService, private _router:Router) { }
  
  ngOnInit() {
    const param = this.route.snapshot.paramMap.get('id');  // taking id from URL ( we are getting in string format)
    if(param){
      const id= +param; // converting in number
      this.getEmployee(id);
    }
  }

  getEmployee(id) {
    this.employeeService.getEmployee(id).subscribe(
      employee => this.employee = employee,
      error => this.errorMessage = <any>error);
}

backtoemployees(){
  this._router.navigate(['/employees'])
}

}
